import uniPopup from "@/components/uni-popup/uni-popup.vue"
import counter from "@/components/counter/counter.vue"
export default {
	components: {uniPopup,counter},
	data() {
		return {
			title: 'Hello',
			num17:1,
			num18:1,
			pageNo:1,
			pageSize:6,
			shopId:"",
			product:[],
			popupData:{},
			size:"未选择",
			skus:[],
			skusId:0,
			trolleyNum:0
		}
	},
	onShow(){
		this.getTrolleyNum();
	},
	onLoad() {
		this.getProduct();
		this.getTrolleyNum();
	},
	methods: {
		chooseSize(i1,i2){
			this.popupData.specification[i1].focus = i2;
			this.priceCal();
		},
		getTrolleyNum(){
			let _this = this;
			this.$ajax("/cart/getCart","POST",{}).then(res=>{
				_this.trolleyNum = res.cartItems.length;
			})
		},
		buy17(s){
			this.$refs.counter.setNum(1);
			this.popupData = s;
			this.skusId = 0;
			this.size = "未选择";
			this.$refs.popup.open();
			
		},
		close17(){
			this.$refs.popup.close()
		},
		trolley(){
			uni.navigateTo({
				"url":"/pages/shop/trolley"
			})
		},
		detail17(s){
			uni.navigateTo({
				"url":"/pages/shop/detail?id="+s.id
			})
		},
		atrolley(){
			
			
			
		},
		priceCal(){
			let _this = this;
			this.skus = [];
			uni.showLoading({
				"title":"计算中",
				"mask":"true"
			});
			_this.size = "";
			if(_this.popupData.skus.length != 0){
				_this.popupData.specification.map((s)=>{
					s.entries.map((i,index)=>{
						if(s.focus == index){
							let data = {
								id:i.id,
								value:i.value
							}
							_this.size = _this.size +" "+i.value;
							_this.skus.push(data);
						}
					})
				})
				
				
				setTimeout(function(){
					console.log(_this.skus);
					_this.popupData.skus.map((s1)=>{
						console.log(s1.specificationValues);
						if(s1.specificationValues == JSON.stringify(_this.skus)){
							_this.popupData.price = s1.price;
							_this.skusId = s1.id;
							
							// console.log(_this.skus);
							console.log(_this.popupData.price);
						}
					})
					setTimeout(function(){
						uni.hideLoading();
					},888)
				},999);
				
			}
			
		},
		setNum(num18){
			if(this.size=='未选择'){
				uni.showToast({
					"title":"请先选择规格",
					"icon":"none"
				})
				this.$refs.counter.setNum(1);
			}else{
				this.num18 = num18;
				this.priceCal()
			}
		},
		addTrolley(){
			let _this = this;
			if(_this.skusId != 0){
				
				this.$ajax("/cart/add","POST",{skuId:_this.skusId,count:_this.num18,orderName:'id',orderType:'desc'}).then((res)=>{
					if(res){
						
						uni.showToast({
							"title":"添加成功，商品正在购物车等着你呢",
							"icon":"none"
						})
						_this.getTrolleyNum()
						_this.$refs.popup.close()
					}else{
						uni.showToast({
							"title":"添加失败",
							"icon":"none"
						})
						_this.$refs.popup.close()
					}
				})
			}else{
				uni.showToast({
					"title":"请选择规格",
					"icon":"none"
				})
			}
			
			
			
			
			
			
		},
		getProduct(){
			let _this = this;
			this.$ajax("/product/page","POST",{pageNo:this.pageNo,pageSize:_this.pageSize}).then(res=>{
				console.log(res);
				if(res){
					uni.showToast({
						"title":"加载成功",
						"icon":"none"
					})
					res.list.map((s)=>{
						s.specification = JSON.parse(s.specification)
						_this.product.push(s)
					})
					// _this.product.push(res.list);
					console.log(_this.product)
					_this.pageNo = _this.pageNo + 1
					console.log(_this.pageNo);
				}
				
			})
			
			
		}
	}
}
